// "use client";


import Link from "next/link";
import { useAuth } from "@/store/useAuth";
import { useCart } from "@/store/useCart";
import { ShoppingCart } from "lucide-react";
import { useEffect } from "react";

export default function Header() {
  const { user, loading, restoreUser, logout } = useAuth();
  const { carts, userId } = useCart();

  useEffect(() => {
    restoreUser();
  }, [restoreUser]);

  if (loading) return null;

  const cartItems = userId ? carts[userId] || [] : [];
  const cartCount = cartItems.reduce((s, i) => s + i.quantity, 0);

  return (
    <header className="bg-white shadow sticky top-0 z-50">
      <div className="max-w-7xl mx-auto flex justify-between items-center p-4">

        <Link href="/" className="text-2xl font-bold text-orange-500">
          üçΩ Food<span className="text-black">Hub</span>
        </Link>

        <nav className="flex items-center gap-6">
          {!user ? (
            <>
              <Link href="/login">Login</Link>
              <Link href="/register" className="bg-orange-500 text-white px-4 py-2 rounded-full">
                Sign Up
              </Link>
            </>
          ) : (
            <>
              <Link href="/restaurant">Restaurants</Link>

              <Link href="/cart" className="relative">
                <ShoppingCart size={22} />
                {cartCount > 0 && (
                  <span className="absolute -top-2 -right-2 bg-orange-500 text-white text-xs px-2 rounded-full">
                    {cartCount}
                  </span>
                )}
              </Link>

              <Link href="/profile" className="w-9 h-9 rounded-full bg-orange-500 text-white flex items-center justify-center font-bold">
                {user.name.charAt(0).toUpperCase()}
              </Link>

              <button onClick={logout} className="text-red-600">
                Logout
              </button>
            </>
          )}
        </nav>
      </div>
    </header>
  );
}
